  height: 56px;
  text-align: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: 1;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    width: 187px;
    height: 96px;
  }
}

@media (min-width: 1200px) {
