  text-align: center;
  padding-top: 38px;
}
@media (min-width: 768px) {
  .page-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 120px;
    padding-left: 60px;
    padding-right: 60px;
    padding-top: 0;
  }
}
