.main-nav {
  position: relative;
  width: 100%;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
    height: 56px;
  }
}

.main-nav__list {
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #ffffff;
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    width: 23px;
    height: 23px;
    border: none;
    top: 16px;
    right: 20px;
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle {
    background: url("../img/icon-menu.svg") center no-repeat;
  }
  .main-nav--closed .main-nav__toggle:hover {
    opacity: 0.6;
  }
  .main-nav--closed .main-nav__toggle:active {
    opacity: 0.3;
  }

  .main-nav--opened {
    position: absolute;
    top: 0;
    z-index: 2;
  }

  .main-nav--opened .main-nav__toggle {
    background: url("../img/icon-cross.svg") center no-repeat;
  }
  .main-nav--opened .main-nav__toggle:hover {
    opacity: 0.6;
  }
  .main-nav--opened .main-nav__toggle:active {
    opacity: 0.3;
  }

  .main-nav--no-js .main-nav__list {
    display: block;
  }
  .main-nav--no-js .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  .main-nav__item:first-child {
    border-top: none;
  }
  .main-nav__item:last-child {
    border-bottom: none;
    position: relative;
  }
  .main-nav__item:last-child::after {
    content: "";
    position: absolute;
    height: 7px;
    width: 100%;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    border: none;
    width: 20%;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(2) {
    margin-right: 20%;
  }
}

.main-nav__link {
  display: block;
  color: #ffffff;
  font-size: 18px;
  line-height: 40px;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px 10px 5px;
  text-align: center;
}
.main-nav__link:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .main-nav__link:hover {
    background-color: #ffffff;
  }
}
.main-nav__link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-nav__link:active {
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    padding-top: 5px;
    font-size: 14px;
    line-height: 26px;
    color: #000000;
  }
  .main-nav__link--current {
    color: #766357;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
  .main-nav__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

